/* src/components/CanceledReservationsTemplate.css */

.canceled-reservations-template {
  font-family: Arial, sans-serif;
  color: #333;
}

.canceled-reservations-template h2 {
  text-align: center;
  margin-bottom: 20px;
}

.canceled-reservations-template .no-data {
  text-align: center;
  color: #777;
  font-style: italic;
  margin-top: 40px;
}

.canceled-reservations-template table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.canceled-reservations-template th,
.canceled-reservations-template td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}

.canceled-reservations-template th {
  background-color: #f0f0f0;
  font-weight: bold;
}

.canceled-reservations-template tbody tr:nth-child(even) {
  background-color: #fafafa;
}

.canceled-reservations-template tbody tr:hover {
  background-color: #eaeaea;
}

/* 원본객실정보 */
.show-original-btn,
.hide-original-btn {
  background: none;
  border: none;
  color: #3498db;
  cursor: pointer;
  font-size: 0.8rem;
  padding: 0;
  margin-top: 4px;
}
.show-original-btn:hover,
.hide-original-btn:hover {
  text-decoration: underline;
}
